* {
    margin: 0;
    padding: 0;
}

body {
    background: #222;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: raleway;

}



.item {
    color: white;
}

.todolist {

    min-width: 200px;
    height: 20px;
    padding: 15px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    cursor: crosshair;
}

.btn-item {
    background: none;
    border: none;
    color: white;
    font-size: 16px;

}


.todoitem {
    color: #222;
    font-size: 24px;
}

input {
    background: none;
    border: 1px #555 solid;
    padding: 15px 15px;
    border-radius: 15px;
    width: 250px;
    color: white;
}

.btn {
    margin-left: 15px;
    background: none;
    border: 1px #555 solid;
    border-radius: 45px;
    color: white;
    padding: 15px 15px;
    width: 50px;
    height: 50px;
}

input {
    outline: none;
}